.transaction {
    width: 600px;
    margin-left: 50px;
}

.row {
    height: 30px;
    text-align: left;
    background-color: #fff;
}

.row:nth-child(even) {
    background-color: #ecf1f4;
}

.head {
    height: 35px;
    composes: row;
    background-color: #00bcd5;
    color: #fff;
}

.wrapper {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
}

:global(td) {
    padding-left: 30px;
}

:global(td) {
    padding-left: 35px;
}

:global(th) {
    padding-left: 35px;
}